import React from "react";
import styled from "styled-components";
import logo from "../img/logo.jpg";

const Course = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(303.5px, 1fr));
  grid-template-rows: 1fr 1fr;
  background: #833ae1;
  height: 300px;
  h1 {
    text-transform: uppercase;
    font-size: 300%;
    color: white;
    margin: 0px;
    margin-top: 10px;
  }
  img {
    justify-self: end;
    width: 80px;
    height: 80px;
    @media (max-width: 720px) {
      display: none;
    }
  }
  p {
    place-self: center center;
    margin: 20px;
    text-align: center;
    text-transform: uppercase;
    font-size: ${props => (props.size ? "36pt" : "120%")};
    color: ${props => (props.color ? "black" : "white")};
  }
  button {
    place-self: center center;
    display: block;
    width: 200px;
    height: 50px;
    background: yellow;
    border-radius: 30px;
    font-size: 120%;
    text-transform: uppercase;
  }
`;

export default () => (
  <Course>
    <h1>Креативное программирование</h1>
    <img src={logo} alt="Алгоритмика" />
    <p>Курс для детей 8-12 лет</p>
    <button>Записаться</button>
  </Course>
);
