import React from "react";
import connect from "@vkontakte/vk-connect";
import styled from "styled-components";
import { createGlobalStyle } from "styled-components";
import Page1 from "./panels/Page1";
import Page2 from "./panels/Page2";
import Page3 from "./panels/Page3";
import { BrowserRouter, Route } from "react-router-dom";

const App = () => {
  const GlobalStyle = createGlobalStyle`
  body {
    margin: 0px;
		font-size: 100%;
  }
`;
  const Wrapper = styled.div`
    display: grid;
    grid-template-rows: 50px 300px 470px 445px 200px 100px;
    grid-template-columns: 795px;
  `;

  return (
    <BrowserRouter>
      <Wrapper>
        <GlobalStyle />
        <Page1 path="./panels/Page1" component={Page1} />
        <Route path="./panels/Page2" component={Page2} />
        <Route path="./panels/Page3" component={Page3} />
      </Wrapper>
    </BrowserRouter>
  );
};

export default App;
